:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --instagram: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --youtube: hsl(348, 97%, 39%);
  --bg-toggle: linear-gradient(hsl(230, 22%, 74%), hsl(230, 22%, 74%));
  --color-bg: hsl(0, 0%, 100%);
  --color-bg-top-pattern: hsl(225, 100%, 98%);
  --color-bg-card: hsl(227, 47%, 96%);
  --color-bg-card-hover: hsl(232, 33%, 91%);
  --color-text: hsl(230, 17%, 14%);
  --color-text-desaturated: hsl(228, 12%, 44%);
}
:root body.dark {
  --bg-toggle: linear-gradient(225deg, hsl(146, 68%, 55%), hsl(210, 78%, 56%));
  --color-bg: hsl(230, 17%, 14%);
  --color-bg-top-pattern: hsl(232, 19%, 15%);
  --color-bg-card: hsl(228, 28%, 20%);
  --color-bg-card-hover: hsl(228, 25%, 27%);
  --color-text: hsl(0, 0%, 100%);
  --color-text-desaturated: hsl(228, 34%, 66%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--color-bg);
  color: var(--color-text);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 3px;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.text-sm {
  font-size: 0.875rem;
}
.text-md {
  font-size: 1.125rem;
}
.text-lg {
  font-size: 2rem;
}
.text-xx-lg {
  font-size: 2.5rem;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  cursor: pointer;
  width: 100%;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  background: var(--color-bg-card);
  transition: background 0.15s ease-in-out;
}
.card:hover {
  background: var(--color-bg-card-hover);
}
@media (min-width: 71.875em) {
  .card {
    width: 15.9375rem;
  }
}
.card .status {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--limegreen);
}
.card .status.down {
  color: var(--brightred);
}
.card .status .caret {
  margin-right: 0.25rem;
}
.card.large {
  height: 13.5rem;
  border-top: solid 5px;
  text-align: center;
}
.card.large.facebook {
  border-color: var(--facebook);
}
.card.large.twitter {
  border-color: var(--twitter);
}
.card.large.instagram {
  position: relative;
  overflow: hidden;
  border: none;
  padding-top: 1.875rem;
}
.card.large.instagram:after {
  content: "";
  display: block;
  position: absolute;
  height: 0.3125rem;
  width: 100%;
  top: 0;
  left: 0;
  background: var(--instagram);
}
.card.large.youtube {
  border-color: var(--youtube);
}
.card.large .user {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.75rem;
}
.card.large .user span {
  font-weight: 700;
  font-size: 0.75rem;
  padding-left: 0.5rem;
  color: var(--color-text-desaturated);
}
.card.large .followers {
  margin-bottom: 1.5625rem;
}
.card.large .followers .count {
  font-weight: 700;
  font-size: 3.5rem;
  margin-bottom: 0.5625rem;
  line-height: 1;
}
.card.large .followers .text {
  font-size: 0.75rem;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  color: var(--color-text-desaturated);
}
.card.small {
  display: flex;
  justify-content: space-between;
}
.card.small > * {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.4375rem;
}
.card.small > *:last-child {
  align-items: flex-end;
}
.card.small .stats .name {
  color: var(--color-text-desaturated);
  font-size: 0.875rem;
}
.card.small .stats .count {
  font-weight: 700;
  font-size: 2rem;
}
.card.small .status {
  font-size: 0.875rem;
}

.toggle {
  display: flex;
  align-items: center;
}
.toggle label {
  color: var(--color-text-desaturated);
  font-weight: 700;
  order: -1;
}
.toggle label:before {
  content: "Dark Mode";
  display: block;
}
.toggle input {
  padding: 3px;
  width: 3rem;
  height: 1.5rem;
  margin-left: 1.125rem;
  border-radius: 1.25rem;
  display: flex;
  appearance: none;
  position: relative;
  background-image: var(--bg-toggle);
  transition: all 0.1s ease-out;
}
.toggle input:after {
  content: "";
  display: block;
  position: absolute;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--color-bg-top-pattern);
  transition: all 0.1s ease-out;
}
.toggle input:checked:after {
  left: 1.6875rem;
}
.toggle input:checked + label:before {
  content: "Light Mode";
}

header {
  margin-top: 2.25rem;
  margin-bottom: 2.875rem;
}
@media (min-width: 40em) {
  header {
    display: flex;
    justify-content: space-between;
  }
}
header span {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--color-text-desaturated);
}

section > .list {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  section > .list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  section > .list {
    grid-template-columns: repeat(4, 1fr);
  }
}
section.overview {
  margin-top: 2.5rem;
}